import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const loadCollections = () => {
  try {
    const coll = localStorage.getItem("collections");
    if (coll === null) {
      return undefined;
    }
    return JSON.parse(coll);
  } catch (err) {
    return undefined;
  }
};

const saveCollections = state => {
  try {
    const coll = JSON.stringify(state);
    localStorage.setItem("collections", coll);
  } catch (err) {
    console.log(err);
  }
};

export default new Vuex.Store({
  state: {
    search_imgs: [],
    fetch_imgs: [],
    collections: loadCollections() || []
  },
  getters: {
    search_imgs: state => state.search_imgs,
    fetch_imgs: state => state.fetch_imgs,
    collections: state => state.collections
  },
  mutations: {
    search_imgs(state, playload) {
      state.search_imgs = playload;
    },
    fetch_imgs(state, playload) {
      state.fetch_imgs = playload;
    },
    add_collections(state, playload) {
      state.collections = [...state.collections, playload];
      saveCollections(state.collections);
    },
    delete_collections(state, playload) {
      state.collections.map((t, i) => {
        if (playload.id === t.id) {
          state.collections = [
            ...state.collections.slice(0, i),
            ...state.collections.slice(i + 1)
          ];
        }
      });
      saveCollections(state.collections);
    },
    remove_all(state) {
      state.collections = "";
      localStorage.removeItem("collections");
    }
  },
  actions: {
    fetchImages(context) {
      axios
        .get("/.netlify/functions/fetchimgs")
        .then(r => {
          context.commit("fetch_imgs", r.data);
        })
        .catch(err => {
          console.log("err");
        });
    },
    SearchImages(context, playload) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/.netlify/functions/searchimgs?q=${playload}`)
          .then(r => {
            context.commit("search_imgs", r.data);
            resolve({ loading: false });
          })
          .catch(err => console.log(err));
      });
    },
    addCollections(context, playload) {
      context.commit("add_collections", playload);
    },
    deleteCollections(context, playload) {
      context.commit("delete_collections", playload);
    }
  }
});
